const CameraHorizontalFlipItem = (props) => {
  const [checked, setChecked] = useState(props.horizontal || false);

  const handleChange = async (event) => {
    setChecked(event.target.checked);
    // props.onHorizontal(event.target.checked);
    const sendData = { cameraHorizontalMirror: event.target.checked };
    try {
      const response = await sendRequest('POST', '/set-cameraHorizontalMirror', sendData, true);
      console.log(':', response);
    } catch (error) {
      console.error('Error:', error);
    }
  };

  return (
    <SettingItem title="Horizontal flip" subtitle="Set horizontal flip">
      <Switch checked={checked} onChange={handleChange} />
    </SettingItem>
  );
}

