const WifiAPNamelItem = (props) => {
  const [name, setName] = useState(props.name);
  const [loading, setLoading] = useState(false);
  const [helperText, setHelperText] = useState("");
  const handleChange = (event) => {
    setName(event.target.value);
  }

  useEffect(() => {
    setName(props.name);
  }, [props.name]);

  const handleChangeCommitted = async () => {
    setLoading(true);
    const sendData = { name: name };
    try {
      const response = await sendRequest('POST', '/set-name', sendData, true);
      console.log(':', response);
      setLoading(false);
      setHelperText("Setting success");
    } catch (error) {
      console.error('Error:', error);
      setHelperText("set-name");
      setLoading(false);
    }
  }
  return (
    <SettingItem title="Name" subtitle="Set Name">
      <Box sx={{ width: 200, marginRight: 4, maxWidth: "70%" }}>
        <TextField
          id="standard-password-input"
          label="Name"
          type="Name"
          autoComplete="current-password"
          variant="standard"
          // defaultValue={name}
          value={name}
          onChange={handleChange}
          // onBlur={handleChangeCommitted}
          helperText={helperText}
          InputProps={{
            endAdornment: (
              <React.Fragment>
                {
                  loading ? <CircularProgress color="inherit" size={20} /> :
                    <IconButton color="primary" aria-label="add to shopping cart" onClick={handleChangeCommitted}>
                      <DoneIcon />
                    </IconButton>
                }
              </React.Fragment>
            ),
          }}
        />
      </Box>
    </SettingItem>
  )
}

